html,
body,
div#root,
.botContainer {
  height: 100%;
  margin: 0;
  background-color: #ffffff;
}

body {
  margin: 0;
}

.botContainer img {
  width: 120px;
  /* margin: 50vh 0 0 50vw;
  transform: translate(-50%, -50%);  */
}

.errorIcon {
  margin: 50vh 0 0 50vw;
  transform: translate(-50%, -50%);
}

.botHeader {
  background-color: #D6002A;
  box-shadow: 0 1px rgba(0, 0, 0, 0.2);
  box-sizing: content-box;
  color: #ffffff;
  font-weight: 500;
  height: 32px;
  left: 0;
  letter-spacing: 0.5px;
  padding: 8px 8px 0 8px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
  font-family: "Segoe UI", sans-serif;
  font-size: 16px;
  clear: both;
}

.botActivityDecorator {
  min-height: 60px;
  position: relative;
}

.botActivityDecorator .botActivityDecorator__content {
  padding-left: 40px;
}

.botActivityDecorator .botActivityDecorator__buttonBar {
  list-style-type: none;
  margin: 0 0 0 10px;
  padding: 0;
  position: absolute;
}

.botActivityDecorator .botActivityDecorator__buttonBar .botActivityDecorator__button {
  background: White;
  border: solid 1px #e6e6e6;
  margin-bottom: 2px;
  padding: 2px 5px 5px;
}

.feedbackModal {
  padding: 5px;
}

.feedbackModal textarea {
  width: 98%;
  height: 50px;
}

.feedbackModal button {
  float: right;
  margin-left: 10px;
}